import React from 'react';
import {Image} from "@fluentui/react-components";
import Box1 from '../images/2.png';
import Box2 from '../images/4.png';
import Box3 from '../images/3.png';
import * as styles from './section1.module.css';
import './layout.css'; 
import { useTranslation } from 'gatsby-plugin-react-i18next';

const Section1 = () => {
    const { t, i18n } = useTranslation();
    const sections = [
        {
            id: 1,
            backgroundImage: Box1,
            altImage: t('sections.section1.altImage'),
            header: t('sections.section1.header'),
            text: t('sections.section1.text'),
            link: '/services/website'
        },
        {
            id: 2,
            backgroundImage: Box2,
            altImage: t('sections.section2.altImage'),
            header: t('sections.section2.header'),
            text: t('sections.section2.text'),
            link: '/services/web-mobile'
        },
        {
            id: 3,
            backgroundImage: Box3,
            altImage: t('sections.section3.altImage'),
            header: t('sections.section3.header'),
            text: t('sections.section3.text'),
            link: '/services/ai-integrations'
        }
    ];

    return (
        <div className={styles.sectionWrapper}>
        <div className={`${styles.section} ${styles.flexHero}`}>
        {sections.map(section => (
            <div className={styles.box} key={section.id} >
                <Image src={section.backgroundImage} className={styles['boxImage']} alt={section.altImage}/>
                <h2>{section.header}</h2>
                <p>{section.text}</p>
                <a href={section.link} className='arrowButton'><span>{t('Read More')}</span></a>
            </div>))}
        </div>
    </div>
    );
}

export default Section1;