import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import Hero from "../components/hero";
import Section1 from "../components/section1";
import { FluentProvider } from '@fluentui/react-components';
import { planktonTheme } from '../planktonTheme';

import MessageSection from "../components/message-section";
import HelpSection from "../components/help-section";
import { graphql } from 'gatsby';
import {useTranslation} from 'gatsby-plugin-react-i18next';


const IndexPage: React.FC = () => {
    const { t, i18n } = useTranslation();

return (
    <Layout>

        {/*style={{backgroundColor: "#F9F9F9", fontFamily: "Poppins", width: "100%"}}*/}
        <FluentProvider theme={planktonTheme}>
            <Hero/>
            {/* <h2>{t('welcome')}</h2>  Print the "welcome" translation */}
            <MessageSection 
              message={t('We thrive on innovation, crafting advanced solutions designed specifically for you.')} 
              backgroundColor="#427898" 
              color="white"/>
            <Section1/>
            {/* <MessageSection message="We are passionate about our work and can develop technically advanced solutions that meet your needs."/> */}
            <HelpSection displayTitle="flex" backgroundColor="#f9f9f9" isIndex={true}/>
            <MessageSection
                message={t('Our dedication and expertise ensure we turn your ideas into impactful digital realities.')}
                backgroundColor="white" 
                color="black"/>
            {/*<Section2 />*/}


        </FluentProvider>
    </Layout>
    );
}
/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
