import React from "react"
import * as styles from './message-section.module.css';

interface MessageSectionProps {
    message: string;
    backgroundColor: string;
    color: string;
  }

const MessageSection: React.FC<MessageSectionProps> = ({message, backgroundColor, color}) => {
    return (
        <div className={styles.messageDivContent} style={{background: backgroundColor, color: color}}>
            <div className={styles.message}>
                {message}
            </div>
        </div>
    );
}

export default MessageSection